body {
  background-color: #282c34;
  color: #fff;
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
}

.todo-list {
  margin-top: 100px;
}

.btn {
  margin: 5px;
  background-color: #61dafb;
  border: 2px solid #61dafb;
}
li {
  text-align: left;
  font-size: 40px;
  list-style: none;
  margin: 0;
  text-overflow: none;
}

li:hover {
  text-decoration: line-through;
}